@media screen and (max-width: 900px){
    .layout{
        /* min-height: 100%; */
        display: flex;
        flex-direction: column;
        /* width: 108%; */
    }
    .computer{
        display: none;
    }
    .computerBar{
        display: none;
    }
    .menu-bars{
        color: #f5f5f5;
    }
    .nav-menu-items{
        display: flex;
        flex-direction: column;
        background-color: rgba(37, 37, 37, 0.9);
            height: 100vh;
        width: 80%;
       align-items: flex-end;

    /* text-align: start; */
    }

    .nav-menu{
        position: fixed;
        display: flex;
        align-items: center;
        overflow: hidden;
        z-index: 1;
        width: 100%;
        height: 100vh;
        top: 0;
        left: 0;
        /* left: -100%; */
    left: -100%;
    position:relative;
    visibility: visible;
    transition: all 0.5s  ease-out;
    z-index: 2;
        /* bottom: 0; */
        }

        .nav-menu.active {
            position: fixed;
            /* left: 0; */
            /* margin-right: -100%; */
            left: 0;
            visibility: visible;
            transition: all 0.5s ease-out;
            /* transition: 2s ease-in-out!important; */
    z-index: 2;
    }


    .nav-menu-items li{
    margin:1rem 2rem 5rem ;
    color: aliceblue;
    height: 0px;

    }


    .nav-menu-items li:hover {
        /* border: 2px  rgba(58, 87, 75,1); */
        /* color: rgba(58, 87, 75,1); */
        color: rgba(224, 255, 49, 0.6)!important;
        /* text-shadow: 1px 1px 3px #ff0000; */
        /* color: rgba(224, 255, 49, 0.6); */
        text-shadow: 1px 1px 3px rgba(224, 255, 49, 0.6), 0 0 6px rgba(224, 255, 49, 0.6), 0 0 9px rgba(224, 255, 49, 0.6);

    }
    /* .icon:hover {

    } */

    dl, ol, ul {
        margin-top: 1rem;
    }


    .nav-text {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0px 8px 0px;
    list-style: none;
    height: 60px;
    }

    .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
    }
    .nav-text a:hover {
    background-color: #1a83ff;
    }


    .navbar-toggle {
    /* margin-top: 100px; */
    height: 40px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    }
    .navbar-toggle .menu-bars {
        margin-top: 10px;


    }

    span {
    margin-left: 16px;
    }
    .phone-navbar{
        background-color: rgba(37, 37, 37, 0.9);
        margin-bottom: 1rem;
        height: 2rem;
        align-items: flex-end;
    }
    .menu-bars{
        padding: 10px 10px;
        float: right;
        font-size: 1.5rem;

    }

    .navbar-toggle {
        float: left!important;
        justify-content: flex-start;
    }
    .nav-menu-items li{
        background-color: rgba(58, 87, 75,1)
    }
    /* .navbar-toggle{
        display: none;
} */

}
@media screen and (min-width: 901px){
    .phoneBar{
        display: none;
    }
    .navigation-bar-phone{
        display: none;
    }
    .nav-menu{
        display: none;
    }
}