.login{
    display: grid;
    place-items: center;
    margin-left: auto;
    margin-right: auto;
    padding-top: 10rem;
    padding-bottom: 10rem;
}

.login > form {
    display: flex;
    flex-direction: column;
}
@media (min-width: 980px) {
.login > form > input {
    width: 35 rem;
    height: 3rem;
    font-size: 2rem;
    padding-left: 1rem;
    margin-bottom: 1rem;
    border-radius:0.5rem ;
}
.forminput {
    width: 35 rem;
    height: 3rem;
    font-size: 2rem;
    padding-left: 1rem;
    margin-bottom: 1rem;
    border-radius:0.5rem ;
}
}
